<template>
  <div>
    <div>
      <!-- 登录弹框 -->
      <el-dialog title="登录" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
        <el-form :model="loginForm">
          <el-form-item label="账号">
            <el-input v-model="loginForm.username" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="密码">
            <el-input type="password" v-model="loginForm.password" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="login">确 定</el-button>
        </span>
      </el-dialog>
    </div>



    <el-header style="text-align: left; font-size: 30px">教务管理系统<el-button id="login"
        @click="dialogVisible = true">登录</el-button>
      <el-dropdown>
        <i class="el-icon-setting" style="margin-right: 15px"></i>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>查看</el-dropdown-item>
          <el-dropdown-item>新增</el-dropdown-item>
          <el-dropdown-item>删除</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>
    <el-container style="height: 500px; border: 1px solid #eee">
      <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
        <el-menu :default-openeds="['1', '3']">
          <el-submenu index="1">
            <template slot="title"><i class="el-icon-message"></i>导航一</template>
            <el-menu-item-group>
              <template slot="title">分组一</template>
              <el-menu-item index="1-1">选项1</el-menu-item>
              <el-menu-item index="1-2">选项2</el-menu-item>
            </el-menu-item-group>
            <el-menu-item-group title="分组2">
              <el-menu-item index="1-3">选项3</el-menu-item>
            </el-menu-item-group>
            <el-submenu index="1-4">
              <template slot="title">选项4</template>
              <el-menu-item index="1-4-1">选项4-1</el-menu-item>
            </el-submenu>
          </el-submenu>
          <el-submenu index="2">
            <template slot="title"><i class="el-icon-menu"></i>导航二</template>
            <el-menu-item-group>
              <template slot="title">分组一</template>
              <el-menu-item index="2-1">选项1</el-menu-item>
              <el-menu-item index="2-2">选项2</el-menu-item>
            </el-menu-item-group>
            <el-menu-item-group title="分组2">
              <el-menu-item index="2-3">选项3</el-menu-item>
            </el-menu-item-group>
            <el-submenu index="2-4">
              <template slot="title">选项4</template>
              <el-menu-item index="2-4-1">选项4-1</el-menu-item>
            </el-submenu>
          </el-submenu>
        </el-menu>
      </el-aside>


      <el-main>
        <el-form :inline="true" :model="searchForm" class="demo-form-inline">
          <el-form-item label="教师">
            <el-input v-model="searchForm.teacher" placeholder="教师"></el-input>
          </el-form-item>
          <el-form-item label="课程">
            <el-select v-model="searchForm.course" placeholder="课程">
              <el-option label="数据结构" value="1"></el-option>
              <el-option label="程序设计基础" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-date-picker v-model="searchForm.date" type="daterange" range-separator="至" start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">查询</el-button>
          </el-form-item>
        </el-form>
        <el-table :data="tableData" border>
          <el-table-column prop="course" label="课程" width="140">
          </el-table-column>
          <el-table-column prop="credit" label="学分" width="120">
          </el-table-column>
          <el-table-column prop="location" label="上课地点">
          </el-table-column>
          <el-table-column prop="time" label="上课时间">
          </el-table-column>
        </el-table>
        <el-pagination background layout="sizes,prev, pager, next ,jumper" @current-change="onCurrentChange" :total="1000">
        </el-pagination>
      </el-main>
    </el-container>


  </div>
</template>

<script>
import axios from 'axios';
export default {
  data() {
    return {
      tableData: [],
      searchForm: {
        teacher: '',
        course: '',
        date: []
      },

      // 控制弹框的显示
      dialogVisible: false,
      // 登录表单的数据
      loginForm: {
        username: '',
        password: ''
      }

    };

  },
  methods: {
    // 登录方法
    login() {
      if (this.loginForm.username == "student" && this.loginForm.password == "student") {
        this.$message({
          type: 'success',
          message: '登录成功!你好,' + this.loginForm.username + "~"
        });
      }
      else if (this.loginForm.username == "teacher" && this.loginForm.password == "teacher") {
        this.$message({
          type: 'success',
          message: '登录成功!你好,' + this.loginForm.username + "~"
        });
      }
      else if (this.loginForm.username == "admin" && this.loginForm.password == "admin") {
        this.$message({
          type: 'success',
          message: '登录成功!你好,' + this.loginForm.username + "~"
        });
      }
      else {
        this.$message({
          type: 'error',
          message: '登录失败!'
        });

      }
      this.dialogVisible = false;
    },
    onSubmit() {
      alert("查询");
    },
    onCurrentChange(val){
      alert("change to page:"+val);
    }


  },
  mounted(){
    axios.get( ).then(result=>{
      this.tableData=result.data.data;
    })
  }


};
</script>

<style>
.el-header {
  font-size: 30px;
  /* 增大字体大小 */
  font-weight: bold;
  /* 加粗字体 */
}

.el-footer {
  background-color: #B3C0D1;
  color: #333;
  text-align: center;
  line-height: 60px;
}

.el-aside {
  background-color: #D3DCE6;
  color: #333;
  text-align: center;
  line-height: 200px;
}

.el-main {
  background-color: #E9EEF3;
  color: #333;
  text-align: center;
  line-height: 160px;
}

body>.el-container {
  margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}

#login {
  color: darkblue;
  float: right;
}
</style>